<template>
    <div>
        <div class="loginNav">
            <img src="../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">{{title}}</p>
        </div>
        <div class="inpBox">
            <div class="inpDiv mt-46">
                <img src="../../assets/login/phone.png" alt="" class="icon">
                <input type="text" class="oinp f34 pl-72 cccc" v-model="phone" placeholder="请输入手机号">
            </div>
            <div class="inpDiv mt-46">
                <img src="../../assets/login/regx.png" alt="" class="icon">
                <input type="text" class="oinp f34 pl-72 cccc" v-model="email" placeholder="请输入手机验证码">
                <div class="getRegxCode f24 c333" @click="sendYan">{{msg}}</div>
            </div>
            <div class="inpDiv mt-46">
                <img src="../../assets/login/pass.png" alt="" class="icon">
                <input type="password" class="oinp f34 pl-72 cccc" v-model="password" placeholder="设置新密码">
            </div>
            <div class="inpDiv mt-46">
                <img src="../../assets/login/pass.png" alt="" class="icon">
                <input type="password" class="oinp f34 pl-72 cccc" v-model="newPswAgain" placeholder="再次确认新密码密码">
            </div>
            
        </div>
        <div class="loginIn f26 cfff text-center" @click="editPassWord">确认</div>
    </div>
</template>

<script>
    export default {
        data () {
            return {
                msg:'获取验证码',
                title: '',
                phone:'',
                email : '',
                password : '',
                newPswAgain : ''
            }
        },
        methods : {
            returns(){
                this.$router.go(-1);//返回上一层
            },
            editPassWord(){
                if(this.phone == ''){
                    this.$toast('请输入手机号');
                    return
                }
                if(this.email == ''){
                    this.$toast('请输入手机验证码');
                    return
                }
                if(this.password == ''){
                    this.$toast('请输入新密码');
                    return
                }
                if(this.password != this.newPswAgain ){
                    this.$toast('二次密码不一致');
                    return
                }
                var that = this;
                var arr = [this.phone.toString(),this.email.toString()];//ca参数手机号和验证码
                Cordova.exec(that.successFunctionEdit, that.failFunctionEdit, "SmsPlugin", "sendSMS", arr);

            },
            sendYan(){//发送后验证码
                if(this.phone == ''){
                    this.$toast('请先输入手机号');
                    return
                }
                var arr = [this.phone.toString()];
                var that = this;
                Cordova.exec(that.successFunction, that.failFunction, "SmsPlugin", "sendSMS", arr);//ca参数手机号
            }
        },
        successFunction(result){
            this.$toast('验证码已发送');
            // localStorage.setItem('zhxy_user',JSON.stringify(result.data));
            // this.$router.push({path:'/indexs'});
        },
        failFunction(msg){
            this.$toast('验证码发送失败');
            // this.$router.push({path:'/login'});
        },
        successFunctionEdit(result){
            var param = {
                phone:this.phone,
                email:this.email,
                password:this.password
            };
            this.$http({url:this.$api.my.updateTeacherPassword},param).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.$toast('修改成功');
                    this.$router.push({path:'/passWordLogin'})
                    // this.data = data.data;
                }else{
                    this.$toast(data.msg);
                }
                
            },err =>{
                this.$toast.clear();
            })
        },
        failFunctionEdit(msg){
            this.$toast('验证码验证失败');
            // this.$router.push({path:'/login'});
        },
        created() {
            if(this.$route.params.type == 'lost'){
                this.title = '忘记密码';
            }else{
                this.title = '修改密码';
            }
        }
    }
</script>

<style scoped>
    .inpDiv{width: 6.9rem;height: 0.9rem;border-radius: 0.9rem;background: #f3f3f3;margin: 0.45rem auto 0;position: relative;overflow: hidden;}
    .mt-145{margin-top: 1.45rem}
    .icon{width: 0.3rem;height: 0.3rem;display: block;position: absolute;top: 0.30rem;left: 0.3rem;z-index: 10;}
    .oinp{border: none;width: 6rem;height: 100%;z-index: 5;position: absolute;top: 0;left: 0;background: #f3f3f3;}
    input::-webkit-input-placeholder{
        color: #ccc;
        font-size: 0.24rem;
    }
    input::-moz-placeholder{
        color: #ccc;
        font-size: 0.24rem;
    }
    input:-ms-input-placeholder{
        color: #ccc;    
        font-size: 0.24rem;
    }
    .getRegxCode{line-height: 0.9rem;position: absolute;right: 0;top: 0;z-index: 15;padding:0 0.4rem}
    .loginIn{height: 0.9rem;width: 6.9rem;border-radius: 0.9rem; background: linear-gradient(to right, #82cbff , #7b7ceb);line-height: 0.9rem; box-shadow: 0 0 10px #f0f9ff;margin: 3rem auto 1rem; }
</style>




